@import './common.scss';

.login_cotainer {
	background-image: url(../dist/images/login_page_bg.png);
	background-size: cover;
	background-repeat: no-repeat;
	padding: 80px 0;
	.login_box {
		padding: 30px 0;
		width: 825px;
		height: 740px;
		margin: 0 auto;
		background-color: #FFFDF6;
		box-shadow: 0px 5px 10px 0px;
		>h2 {
			margin: 0;
			font-size: 28px;
		}
		>p {
			margin-top: 30px;
			font-size: 30px;
			text-align: center;
			font-family: "Noto Sans KR", sans-serif;
			letter-spacing: -0.8px;
		}
		.solid {
			display: block;
			width: 405px;
			height: 3px;
			margin: 10px auto 40px;
			background-color: #bbbbbb;
			user-select: none;
		}
		#login_form {
			width: 405px;
			margin: 0 auto;
			.id_n_pw {
				position: relative;
				.bar {
					display: block !important;
					width: 100%;
					height: 50px;
					background-color: #d9d9d9;
					outline: none;
					border: none;
					margin-bottom: 15px;
					padding-left: 20px;
					user-select: none;
					&:focus {
						border: 1px solid black;
						background-color: #fff;
					}
				}
				li {
					position: relative;
					>.false_on {
						border: 1px solid red !important;
						background-color: pink !important;
					}
					>.guide_message {
						display: none;
						margin-left: 15px;
						margin-bottom: 15px;
						font-size: 1.2rem;
						color: red;
					}
					>#pw_show_block_btn {
						display: none;
						&:checked~.pw_show_block_btn_design {
							background-image: url(../dist/images/pw_show_icon.png);
							transform: translateY(2px);
						}
					}
					>.pw_show_block_btn_design {
						position: absolute;
						display: block;
						top: 15px;
						right: -50px;
						width: 30px;
						height: 30px;
						background-image: url(../dist/images/pw_block_icon.png);
						background-size: contain;
						background-repeat: no-repeat;
						cursor: pointer;
					}
				}
			}
			.login_save_n_search {
				display: flex;
				align-items: center;
				justify-content: space-between;
				font-family: "Noto Sans KR", sans-serif;
				font-size: 1.2rem;
				letter-spacing: -0.8px;
				input {
					display: none;
				}
				#id_rm_chk {
					&:checked~.id_rm {
						&::after {
							background-image: url(../dist/images/check_icon.png);
							background-color: lightgray;
							background-size: contain;
							background-repeat: no-repeat;
						}
					}
				}
				.id_rm {
					position: relative;
					font-size: 1.4rem;
					margin-left: 30px;
					cursor: pointer;
					&::after {
						content: "";
						position: absolute;
						top: 0;
						left: -30px;
						width: 20px;
						height: 20px;
						background-color: white;
						border: 1px solid #bbbbbb;
						transition: all 0.2s;
					}
				}
			}

			.sign_in_n_sign_up {
				width: 405px;
				margin: 40px auto 0;
				>li {
					width: 100%;
					height: 55px;
					margin-bottom: 15px;
					text-align: center;
					user-select: none;
					&:first-child {
						background-color: #ffbf00;
					}
					&:last-child {
						background-color: #4136BF;
					}
					p {
						display: block;
						line-height: 55px;
						font-size: 1.6rem;
						color: #fff;
						font-weight: 700;
						font-family: "Noto Sans KR", sans-serif;
						letter-spacing: -0.8px;
						cursor: pointer;
					}
					a {
						display: block;
						line-height: 55px;
						font-size: 1.6rem;
						color: #fff;
						font-weight: 700;
						font-family: "Noto Sans KR", sans-serif;
						letter-spacing: -0.8px;
						cursor: pointer;
					}
				}
			}
			.easy_notice {
				text-align: center;
				margin-top: 40px;
				font-size: 1.6rem;
			}
			.sns_login {
				display: flex;
				justify-content: center;
				margin-top: 20px;
				li {
					margin-right: 30px;
					&:last-child {
						margin-right: 0;
					}
					a {
						display: inline-block;
						width: 40px;
						height: 40px;
						user-select: none;
					}
				}
			}
		}
	}
}

